*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color:white;
}

body{
    background-color: black;
}


/*header part*/
.navbar{
    width: 100%;
   padding: 1%;
    display: flex;
    background-color: #000;
    justify-content: space-between;
    align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.left-nav{
     display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    gap: 10px;
     flex-shrink: 0;
}
.left-nav h1{
    font-family: sans-serif;
    font-weight: 600;
    font-size: medium;
}
.left-nav img{
    background-color: aliceblue;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
}
.right-nav{
    
   
    display: flex;
    gap: 30px;   
    padding-right: 50px;
    flex-shrink: 0;
    align-items: center;
}
.navtext{
    display: flex;
    gap: 25px;
}

h5{
    height: 19px;
    font-family: sans-serif;
}

.btns{
  display: flex;
  gap: 9px;
}
button {
        height: 35px;
        width: 80px;
   white-space: nowrap;
   border-radius: 10px;
    background-color: gold;
    align-items: center;
    border: none;
    cursor: pointer;
}



button> h5 {
     font-size: 13px;
    color: black;
    font-family: sans-serif;
}
.btns .mobbutton{
    display: none;
    gap: 7px;
    align-items: center;

}

.upgrade{
 
   height: 32px;
   width: 78px;
   white-space: nowrap;
   border-radius: 8px;
    background-color: gold;
    align-items: center;
    border: none;
}



.arrowbutton{
      height: 28px;
        width: 34px;
   white-space: nowrap;
   border-radius: 5px;
    background-color: gold;
    align-items: center;
    border: none;
}
.arrowbutton i{
  color: #000;
  font-size: 20px;
}
/*search box and middle header*/
.middleheader{
    width: 100%;
 
 display: flex;
  flex-direction: column;
  position: fixed;
  position: fixed;
  top: 12vh;
  left: 0;
  z-index: 999;

}
.text-content{
    width: 100%;
    height: 55%;
    background-color: black;
 display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
}
.text-content h3{
  font-family: sans-serif;
  font-size: 1rem;
  color: rgb(136, 143, 148);
}
.seachbox{
    
    width: 100%;
    background-color: black;
    display: flex;
 justify-content: center;
 align-items: center;
}
.seachbox input{
  
 width: 80%;
 height: 40px;
 border: 1px solid;
 border-radius: 20px;
 background-color:oklch(0.269 0 0);
   padding: 10px 15px;
   font-size: 0.9em;
}
.favicon{

        height: 45px;
        width: 45px;
   white-space: nowrap;
   border-radius: 50%;
    background-color: yellow;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1%;
}
.favicon i{
   
color: #000;
font-size: 22px;
}

/*laganchart part*/

.lagna{
     width: 32vmax;
  height: 32vmax;
  border: 2px gold solid;
  overflow: hidden;
   margin: 10vh auto;
margin-top: 15vw;
   z-index: -1;
}

.lagnachart{
position: relative;
  width: 34.3vmax;
  height: 34.5vmax;
  background: rgb(0, 0, 0);
  border: 1px solid gold;
  overflow: hidden;
  display: flex;
  clip-path: inset(0 8% 8% 0);
}
.lagnachart::before{
    content: "";
    position: absolute;
  
  inset: 0;
    pointer-events: none;
   z-index: 5;
   box-sizing: border-box;
}

 .househ1  {
    position: absolute;
  width: 33%;
  height: 33%;
  background: #222;
  color: gold;
  border: 2px solid gold;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
transform: translate(91%, 20%) rotate(-45deg);
    
 }


 .househ1 span{
    color: gold;
    padding: 10%;
     transform: rotate(45deg);
   
 }
 .house-planeth1{
    display: flex;
    gap: 2vmax;
 }

