/*bottom part*/
.bottompart{
    display: flex;
    align-items: center;
    width: 90%;
    margin: 0 auto;
    height: 10vmax;
    max-width: 1500px;
 padding-bottom: 1vh;
}
.h4text{
    max-width: 700px;
   padding: 18px;
    background-color:oklch(0.269 0 0);
    display: flex;
    align-items: start;
    font-family: sans-serif;
    flex-wrap: wrap;
   
}
.h4text h4{
    padding-bottom: 15px;
}
.info{
     flex: 2;
    display: flex;
  margin-left: 2%;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: start;
  border: 1px solid white;
  padding: 15px;
}
.bottompart .info button{
    width: 105px;
    height: 35px;
      font-size: 0.9rem;
    background-color: #222;
    border: none;
}

@media (max-width:600px) {
 
  .navtext {
    display: none;
  }
.btns  .mobbutton{
    display: flex;
  }
 .middleheader{
  top: 10vh;
 }
 .ls{
  display: none;
 }

   .text-content {
    overflow-x: auto;    
    white-space: nowrap;  
    -webkit-overflow-scrolling: touch; 
    scrollbar-width: none; 
  }
  .lagna{
     
    width: 90vmin;
    height: 90vmin;
   margin-top: 25vh;
     margin-bottom: 5vh;
 
  }
  .lagnachart{
    width: 97vmin;
  height: 97vmin;
  }
  .bottompart{

  flex-direction: column;
   height: auto;
    

}

  .text-content h3 {
    display: inline-block; 
    padding: 0 15px;      
    font-size: 0.95rem;
  }
.h4text{
    width: 95%;

    gap: 10px;
    padding: 2%;
  }
  .h4text h4{
 padding: 2%;
  }
  .info{
   margin-top: 2%;
      width: 95%;
  margin-left: 1%;
  flex-wrap: wrap;
  gap: 2%;
  padding: 1%;
 border: none;
}
.bottompart .info button{
   
  width: 30%;
 margin: 1%;
 font-size: 0.8rem;
}
}